html {
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  line-height: 16px;
}

*,
p,
button,
input,
a,
ul {
  box-sizing: border-box;
  padding: 0;
  margin: 0;

  list-style: none;
  text-decoration: none;
  border: none;
}

main {
  background-color: $colorMain;
}
