@media (max-width: $tabletWidth) {
  .nav-burger {
    display: flex;
    flex-direction: column;

    &__list {
      position: absolute;
      top: $mobilePositionNavTop;

      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: calc(
        100vh - #{$mobilePositionNavTop} - #{$mobilePositionNavBott}
      );
    }

    &__item {
      &:last-child {
        display: flex;
        flex-direction: column;

        > :last-child {
          margin-top: 16px;
        }
      }
    }

    &__link {
      @include linkBurgerMenu;

      &:hover {
        color: $colorInfo;
      }
    }

    &__contact {
      @include linkHeader($fontWeight: bold);
    }
  }
}
